import { Routes } from '@angular/router';
import { createGuardedGridRoute } from '../../../../shared/utils';
import { clientTypePermissionGuard } from '../../guards/client-type-permission.guard';
import { createSmartRedirectGuard } from '../../guards/smart-redirect.guard';
import { AdminGridContainerComponent } from '../admin-grid-container/admin-grid-container.component';
import { AdminTreetableContainerComponent } from '../admin-treetable-container/admin-treetable-container.component';
import { ClientContainerComponent } from './client-container/client-container.component';
import {
  clientAlertsGuard,
  clientAssetsGuard,
  clientGeofencesGuard,
} from '../../guards';
import { CLIENT_ADMIN_FEATURES } from './client-administration.config';

export const ADMIN_CLIENT_ROUTES: Routes = [
  {
    path: '',
    component: ClientContainerComponent,
    children: [
      {
        path: 'accounts',
        children: [
          {
            path: '',
            canActivate: [
              createSmartRedirectGuard('accounts', [
                'client-users',
                'client-user-roles',
              ]),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'details',
        loadChildren: () =>
          import('./client-details/client-details.routes').then(
            (m) => m.ADMIN_CLIENT_DETAILS_ROUTES,
          ),
        data: { nestedContentScrollable: true },
      },
      {
        path: 'hardware',
        children: [
          {
            path: '',
            canActivate: [
              createSmartRedirectGuard('hardware', [
                'client-hardware-devices',
                'client-asset-tags',
                'client-sim-cards',
              ]),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'organization',
        children: [
          {
            path: '',
            canActivate: [
              createSmartRedirectGuard('organization', [
                'client-cost-centres',
                'client-asset-groups',
                'client-asset-categories',
                'client-geofence-groups',
              ]),
            ],
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type',
            canActivate: [clientTypePermissionGuard],
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [clientTypePermissionGuard],
            component: AdminTreetableContainerComponent,
          },
        ],
      },
      {
        path: 'profiles',
        children: [
          {
            path: '',
            canActivate: [
              createSmartRedirectGuard('profiles', [
                'client-io-types',
                'client-device-config-profiles',
                'client-asset-rating-profiles',
                'client-asset-state-profiles',
                'client-overspeed-profiles',
                'client-road-profiles',
                'client-geo-lock-profiles',
                'client-custom-fields',
              ]),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'providers',
        children: [
          {
            path: '',
            canActivate: [
              createSmartRedirectGuard('providers', [
                'client-sms-gateway-providers',
              ]),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [clientTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      ...createGuardedGridRoute(
        'assets',
        clientAssetsGuard,
        AdminGridContainerComponent,
        CLIENT_ADMIN_FEATURES['assets'].permissions,
        undefined,
        'grid/:type',
        'client',
      ),
      ...createGuardedGridRoute(
        'geofences',
        clientGeofencesGuard,
        AdminGridContainerComponent,
        CLIENT_ADMIN_FEATURES['geofences'].permissions,
        undefined,
        'grid/:type',
        'client',
      ),
      ...createGuardedGridRoute(
        'alerts',
        clientAlertsGuard,
        AdminGridContainerComponent,
        CLIENT_ADMIN_FEATURES['alerts'].permissions,
        undefined,
        'grid/:type',
        'client',
      ),
      {
        path: '',
        redirectTo: 'details',
        pathMatch: 'full',
      },
    ],
  },
];
