import { CanActivateFn, Router } from '@angular/router';
import { inject } from '@angular/core';
import { ClientPermissionsService } from '../services/client-permissions.service';
import { CLIENT_ADMIN_FEATURES } from '../components/clients/client-administration.config';

/**
 * Guard that validates permissions for dynamic type routes.
 * Extracts the :type parameter and looks up required permissions
 * from CLIENT_ADMIN_FEATURES, ensuring consistency with tab permissions.
 */
export const clientTypePermissionGuard: CanActivateFn = async (route) => {
  const clientPermissionsService = inject(ClientPermissionsService);
  const router = inject(Router);
  const type = route.paramMap.get('type');

  if (type === null || type === '') return true;

  if (!(type in CLIENT_ADMIN_FEATURES)) return true;

  const featureConfig =
    CLIENT_ADMIN_FEATURES[type as keyof typeof CLIENT_ADMIN_FEATURES];

  if (!('permissions' in featureConfig)) {
    return true;
  }

  const hasAccess = clientPermissionsService.canAccessFeature(
    featureConfig.permissions,
  );

  if (!hasAccess) {
    await router.navigate(['/unauthorized'], {
      queryParams: {
        requiredPermission: JSON.stringify(featureConfig.permissions),
      },
    });
    return false;
  }

  return true;
};
